// src/index.js (Versi Final - Production-Ready, Multi-Tenant)

export default {
  async fetch(request, env) {
    // Handle preflight requests for CORS
    if (request.method === 'OPTIONS') {
      return new Response(null, { status: 204, headers: corsHeaders() });
    }

    const url = new URL(request.url);
    const { pathname } = url;
    const { accountId, userId } = getIdentity(request);
    const db = env.DB;

    try {
      // ========== ALERTS ENDPOINTS ==========
      if (pathname === '/alerts') {
        if (request.method === 'GET') {
          const { results } = await db.prepare("SELECT * FROM v_alerts_active WHERE account_id = ? AND user_id = ? ORDER BY timestamp DESC")
            .bind(accountId, userId).all();
          return json(results);
        }
        if (request.method === 'POST') {
          const body = await request.json(); 
          const id = body.id || `alert_${Date.now()}`;
          await db.prepare(`INSERT INTO alerts (id, timestamp, pest_type, severity, description, affected_crops, location, coordinates, photo_name, account_id, user_id, created_at, created_by, updated_at, updated_by) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)`)
            .bind(id, body.timestamp, body.pest_type, body.severity, body.description, body.affected_crops, body.location, JSON.stringify(body.coordinates), body.photo_name, accountId, userId, new Date().toISOString(), userId, new Date().toISOString(), userId).run();
          return json({ success: true, id }, 201);
        }
      }

      // ========== DIAGNOSIS ENDPOINTS ==========
      if (pathname.startsWith('/diagnosis')) {
        if (request.method === 'GET') {
          const { results } = await db.prepare("SELECT * FROM v_diagnosis_active WHERE account_id = ? AND user_id = ? ORDER BY timestamp DESC")
            .bind(accountId, userId).all();
          return json(results.map(parseDiagnosis));
        }
        if (request.method === 'POST') {
          const formData = await request.formData();
          const submissionData = {};
          for (const [key, value] of formData.entries()) { submissionData[key] = value; }
          const photoFile = submissionData.photo instanceof File ? submissionData.photo : null;
          const mockAiResult = {
            diagnosis: { label: 'Bercak Daun (dari Server D1)', confidence: 92.3, severity: 'sedang', description: 'Disimpan permanen di D1.' },
            recommendations: [ { id: 'rec_d1_1', title: 'Rekomendasi Server 1', description: 'Deskripsi 1.', priority: 'tinggi', timeframe: '1-2 hari' } ]
          };
          const id = `diag_${Date.now()}`;
          const now = new Date().toISOString();
          await db.prepare(`INSERT INTO diagnosis (id, timestamp, field_id, crop_type, latitude, longitude, notes, photo_name, result_label, result_confidence, result_severity, result_description, recommendations, account_id, user_id, created_at, created_by, updated_at, updated_by) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)`)
            .bind(id, submissionData.timestamp || now, submissionData.field_id, submissionData.crop_type, submissionData.latitude, submissionData.longitude, submissionData.notes, photoFile ? photoFile.name : null, mockAiResult.diagnosis.label, mockAiResult.diagnosis.confidence, mockAiResult.diagnosis.severity, mockAiResult.diagnosis.description, JSON.stringify(mockAiResult.recommendations), accountId, userId, now, userId, now, userId).run();
          return json({ success: true, ...mockAiResult }, 201);
        }
      }

      // ========== EVENTS ENDPOINTS ==========
      if (pathname.startsWith('/events')) {
        const id = pathname.split('/')[2];
        if (request.method === 'GET' && !id) {
          const { results } = await db.prepare("SELECT * FROM v_events_active WHERE account_id = ? AND user_id = ? ORDER BY start_at ASC")
            .bind(accountId, userId).all();
          return json(results.map(parseEvent));
        }
        if (request.method === 'POST' && !id) {
          const body = await request.json();
          validateRequired(body, ['id', 'title', 'start_at']);
          const now = new Date().toISOString();
          await db.prepare(`INSERT INTO events (id, title, type, crop, location, notes, start_at, status, source_type, source_id, account_id, user_id, created_at, created_by, updated_at, updated_by) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)`)
            .bind(body.id, body.title, body.type, body.crop, body.location, JSON.stringify(body.notes || []), body.start_at, 'pending', body.source_type, body.source_id, accountId, userId, now, userId, now, userId).run();
          return json({ success: true, id: body.id }, 201);
        }
        if (request.method === 'PATCH' && id) {
          const body = await request.json();
          const completed_at = body.status === 'completed' ? new Date().toISOString() : null;
          await db.prepare("UPDATE events SET status = ?, completed_at = ?, updated_by = ?, updated_at = datetime('now') WHERE id = ? AND account_id = ? AND user_id = ?")
            .bind(body.status, completed_at, userId, id, accountId, userId).run();
          return json({ success: true });
        }
        if (request.method === 'DELETE' && id) {
           await db.prepare("UPDATE events SET deleted_at = datetime('now'), updated_by = ? WHERE id = ? AND account_id = ? AND user_id = ?")
            .bind(userId, id, accountId, userId).run();
          return new Response(null, { status: 204, headers: corsHeaders() });
        }
      }

      return json({ error: 'Not Found' }, 404);
    } catch (err) {
      console.error(err);
      return json({ error: err.message || 'Server error' }, 500);
    }
  },
};

// ---------- Helpers ----------
const getIdentity = (request) => ({ accountId: request.headers.get('X-Account-Id') || 'demo', userId: request.headers.get('X-User-Id') || 'demo' });
const corsHeaders = () => ({ 'Access-Control-Allow-Origin': '*', 'Access-Control-Allow-Headers': 'content-type, x-account-id, x-user-id', 'Access-Control-Allow-Methods': 'GET, POST, PATCH, DELETE, OPTIONS' });
const json = (data, status = 200) => new Response(JSON.stringify(data), { status, headers: { 'Content-Type': 'application/json', ...corsHeaders() } });
const validateRequired = (obj, fields) => { const miss = fields.filter(f => obj[f] === undefined || obj[f] === null); if (miss.length) throw new Error(`Missing required field(s): ${miss.join(', ')}`); };
const parseMaybeJson = (v) => { try { return JSON.parse(v); } catch (e) { return v; } };
const parseEvent = (event) => ({ ...event, notes: parseMaybeJson(event.notes) });
const parseDiagnosis = (diag) => ({ ...diag, notes: parseMaybeJson(diag.notes), recommendations: parseMaybeJson(diag.recommendations) });